.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.footer-swiggle {
    width: 58.4rem;
}

.footer-main {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.footer-card {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 2rem;
    text-align: center;

    padding: 1rem;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 28rem;
    width: 28rem;
}

.footer-card a:link {
    background: linear-gradient(270deg, #A9FE81 100%, #C0F0FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.footer-card img {
    margin-bottom: 1rem;
    width: 5rem;
}