#nav-container {
    height: 10rem;
}

#click-here {
    position: absolute;
    top: 2rem;
    left: 53.5%;
    width: 16rem;
    z-index: -1;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.6rem;
}

.fab-btn:hover {
    cursor: pointer;
    transform: scale(1.1);
}

.fab-icon {
    width: 10rem;
}

.nav-btn {
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.3s;
}

.nav-btn:hover {
    cursor: pointer;
}

.navbar.open .nav-btn {
    pointer-events: all;
    opacity: 1;
}

.nav-btn-icon {
    width: 8rem;
}

/* Floating Navbar */
.navbar.float {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: fixed;
    right: 0.1rem;
    bottom: 0.1rem;
}

.navbar.float .fab-icon {
    width: 8rem;
}

.navbar.float .nav-btn-icon {
    width: 6rem;
}