.achievements {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.achievements-header {
    margin-top: 1rem;
    /* font-family: 'DM Sans', sans-serif; */
    background: linear-gradient(270deg, #BFF0FF 11.87%, #82B4FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.achievements-main {
    width: 60rem;
    margin: 4rem 0 2rem 0;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 2rem;
    text-align: center;
}

.achievements-main p  {
    margin: 1rem 0;
}