.courses {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.courses-header {
    margin-top: 1rem;
    /* font-family: 'DM Sans', sans-serif; */
    background: linear-gradient(270deg, #DBA6F4 11.87%, #A981FE 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.courses-main {
    margin: 4rem 0 8rem 0;
    width: 60rem;
    display: grid;
    gap: 1rem;
    grid-template-areas: 
        "a b"
        "c d";
}

.card {
    display: flex;
    width: 40rem;

    font-family: 'DM Sans', sans-serif;
}

.card-title {
    font-size: 2rem;
    font-weight: 700;

    background: linear-gradient(270deg, #DBA6F4 11.87%, #A981FE 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.card-subtitle {
    font-size: 1.5rem;
}

.card-icon {
    width: 5rem;
    margin-right: 1rem;
}
