.education {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.education-header {
    display: flex;
    margin: 0.5rem 0;
    padding: 1rem;
    justify-content: center;
    align-items: flex-start;
}

.education-header h2 {
    margin: 2rem 0 0 0;
}

.education-header img {
    width: 10rem;
}

.education-arrow {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 85rem;
}

.education-arrow-text {
    position: absolute;
    top: 10rem;
    right: 0;

    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 3.2rem;
    width: 20rem;

    /* Gradient/Sky */
    background: linear-gradient(270deg, #BFF0FF 11.87%, #82B4FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.education-main {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.education-main > * {
    margin: 2rem 6rem;
}

.education-main img {
    width: 45rem;
}

.education-card {
    margin: 2rem 0;
    font-family: 'DM Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    width: 58rem;
}

.education-card h4 {
    font-size: 4rem;
    word-wrap: break-word;
    background: linear-gradient(270deg, #BFF0FF 11.87%, #82B4FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.education-card p {
    font-size: 2rem;
}