:root {
    --black: #000000;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}

body {
    background-color: var(--black);
    color: white;
    font-family: 'Space Mono', monospace;
}

h2 {
    font-family: 'Space Mono';
    font-style: normal;
    font-weight: 700;
    font-size: 4.8rem;
    line-height: 6.4rem;
    text-align: center;
}