.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.projects-header {
    margin: 2rem 0;
}

.projects-arrow {
    width: 45rem;
}

.project {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.project-main {
    margin: 2rem 0.5rem;
    font-family: 'DM Sans', sans-serif;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 2rem;
    width: 58rem;
}

.project-tech-stack {
    margin: 0.5rem 0 1rem 0;
}

.project-desc {
    font-weight: 400;
}

.project-desc p {
    margin: 2rem 0;
}

.project-title {
    font-size: 4rem;
    /* background: linear-gradient(270deg, #FF4646 11.87%, #FF9486 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent; */
}

.project-img {
    margin: 2rem 0.5rem;
    width: 55rem;
}