.skills {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

/* .skills-header {
    
} */

.skills-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100rem;
    margin: 8rem 0 0 0;
}

.chip {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0.5rem 0.5rem;
    padding: 1rem 3rem;
}

.chip::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 12rem; 
  border: 0.3rem solid transparent;
  background: linear-gradient(270deg, #DBA6F4 11.87%, #A981FE 100%) border-box;
  -webkit-mask:
    linear-gradient(#fff 0 0) padding-box, 
    linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}

.chip-text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 2.4rem;
    line-height: 3.2rem;

    background: linear-gradient(270deg, #FFE597 11.87%, #AAFF82 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.skills-arrow {
    width: 30rem;
}